@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700,800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-header{
	background-color: white;
	height: 65px;
	font-family: 'Open Sans', sans-serif;
	font-weight: 800;
	font-size: 18pt;
	color: #003459;
	padding-bottom: 16px;
	padding-top: 16px;
	box-sizing: border-box;
	line-height: 37px;
	margin-left: 25px;
	margin-right: 25px;
	letter-spacing: 2px;
}

.sidebar-open-button{
	display: none;
}

#header-icon{
	height: 24px;
	position: relative;
    top: 3px;
    margin-right: 10px;
}

.nav-items{
	display: inline-block;
}

.nav-items>div{
	display: inline-block;
	font-weight: 100;
	font-size: 12pt;
	margin-left: 32px;
}

.nav-item{
	cursor: pointer;
}

@media (max-width: 768px) {
	.nav-items{
		display: none;
	}

	.nav-header{
		padding-bottom: 8px;
		padding-top: 8px;
		height: auto;
		height: initial;
		margin-left: 10px;
		margin-right: 10px;
	}

	.sidebar-open-button{
		display: inline;
		display: initial;
	}
}
.nav-sidebar{
	height: 100vh;
	background-color: white;
	top: 0;
	position: absolute;
	width: 200px;
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	-webkit-transition: all 0.5s ease;
	transition: all 0.5s ease;
	box-shadow: 4px 0px 4px 0px rgba(0,0,0,0.1);
}

.closed{
	-webkit-transform: translateX(-105%);
	        transform: translateX(-105%);
}

.sidebar-items-container div{
	border-bottom: 1px solid gray;
	padding: 10px;
	color: #003459;
	cursor: pointer;
}

.sidebar-items-container div:first-child{
	border-top: 1px solid gray;	
}

.sidebar-close-button {
	text-align: right;
	font-size: 18pt;
	padding: 10px;
	font-weight: 800;
	color: #003459;
	cursor: pointer;
	display: inline-block;
    position: relative;
    left: calc(100% - 38px);
}

@media (min-width: 768px) {
	.nav-sidebar{
		display: none;
	}
} 
.mobile-day-banner{
	width: 100%;
	background: -webkit-linear-gradient(left, #2E96FF, #28C2FF);
	background: linear-gradient(to right, #2E96FF, #28C2FF);
	padding-bottom: 8px;
	padding-top: 8px;
	box-sizing: border-box;
	color: white;
	text-align: center;
	font-family: 'Open Sans', sans-serif;
}

.day-banner-city{
	font-size: 18pt;
	line-height: 18pt;
	margin-bottom: 4px;
}

.day-banner-weather{
	font-size: 14pt;
	line-height: 14pt;
	margin-bottom: 16px;
}

.day-banner-temp{
	font-size: 56pt;
	margin-bottom: 4px;
}

.day-banner-feeling{
	font-size: 12pt;
	line-height: 12pt;
}

.day-banner-weather-icon{
	height: 24px;
	-webkit-filter: grayscale(100%) brightness(100000000%);
	        filter: grayscale(100%) brightness(100000000%);
	position: relative;
    top: 4px;
    margin-right: 10px;
}
.mobile-day-forecast{
	width: 100%;
	background-color: white;
	-webkit-transition: height 0.5s ease;
	transition: height 0.5s ease;
	height: 50px;
	overflow: hidden;
	border-bottom: 1px solid #E8E9EB;
	padding-top: 16px;
}

.expanded{
	height: 127px;
}

.weather-icon{
	height: 32px;
	-webkit-filter: hue-rotate(107deg) saturate(10000%) brightness(0.465);
	        filter: hue-rotate(107deg) saturate(10000%) brightness(0.465);
	margin-left: 5px;
    margin-right: 5px;
}

.forecast-day-container{
	width: calc(100% - 42px);
	font-size: 12pt;
    color: #003459;
    font-family: 'Open Sans', sans-serif;
}

.forecast-week-day{
    font-weight: 800;
}

.forecast-day-high{
	font-size: 14pt;
}

.forecast-day-weather {
	color: #97A2AA;
}

.forecast-day-low{
	font-size: 12pt;
	position: relative;
    right: 4px;
}

.expanded-info{
	margin-top: 8px;
	margin-bottom: 8px;
	margin-left: 42px;
	color: #97A2AA;
}

.expanded-info div{
	margin-bottom: 5px;
}

.expanded-info div:last-child{
	margin-bottom: 0;
}
.mobild-forecast-container{
	margin-left: 20px;
	margin-right: 20px;
}
.forecast-title{
	font-family: 'Open Sans', sans-serif;
	font-size: 12pt;
	color:#003459;
	margin-top: 16px;
    font-weight: 800;
    margin-left: 20px;
}
.desktop-day-banner{
	background: -webkit-linear-gradient(left, #2E96FF, #28C2FF);
	background: linear-gradient(to right, #2E96FF, #28C2FF);
	padding: 32px;
	color: white;
	font-family: 'Open Sans', sans-serif;
	display: flex;
}

.daybanner-left-col{
	display: inline-block;
}

.daybanner-right-col{
	display: inline-block;
	margin-left: 32px;
	flex-grow: 1;
}

.day-banner-city-desktop{
	font-size: 26px;
	line-height: normal;
	line-height: initial;
}

.desktop-day-info>div{
	display: inline-block;
	margin-right: 32px;
	margin-bottom: 8px;
}

.desktop-day-info>div:last-child{
	margin-right: 0px;
}

.info-title{
	font-weight: 800;
	font-size: 16px;
}
.tabbed-forecast{
	margin-top: 32px;
}

.tabbed-forecast .title{
	font-family: 'Open Sans', sans-serif;
	color: #003459;
	font-size: 16pt;
	font-weight: 700;
	margin-bottom: 10px;
}

.tabs-container{
	display: flex;
	flex-direction: row;
}


.tabs-container>div:first-child{
	margin-left: 0px;
}

.detail-pane{
	margin-top: 8px;
	background-color: #003459;
	padding-left: 64px;
	padding-right: 64px;
	padding-top: 32px;
	padding-bottom: 32px;
}

.detail-pane>div{
	display: inline-block;
	color: white;
	margin-right: 64px;
	font-family: 'Open Sans', sans-serif;
}

.detail-pane>div>div:first-child{
	font-weight: 700;
}
.desktop-tabs{
	border: 1px solid #003459;
	height: calc(121px - 32px);
	flex-grow: 1;
	margin-left: 32px;
	padding-top: 16px;
	padding-bottom: 16px;
	position: relative;
	text-align: center;
	flex-basis: 0;
}

.desktop-tabs .tab-day{
	font-size: 12pt;
	font-weight: 700;
	color: #003459;
	font-family: 'Open Sans', sans-serif;
}

.desktop-tabs.selected .tab-day{
	color: white;
}

.high-low{
	color: #003459;
}

.desktop-tabs.selected .high-low{
	color:white;
}

.high-low-separator{
	width: 16px;
}

.tab-connector{
	height: 8px;
    width: calc(100% + 2px);
    background-color: #003459;
    position: absolute;
    bottom: -9px;
    left: -1px;
    display: none;
}

.tab-icon>img{
	height: 32px;
	-webkit-filter: hue-rotate(107deg) saturate(10000%) brightness(0.465);
	        filter: hue-rotate(107deg) saturate(10000%) brightness(0.465);
	margin-top: 7px;
    margin-bottom: 7px;
}

.desktop-tabs.selected .tab-icon>img{
	-webkit-filter: grayscale(100%) brightness(100000000%);
	        filter: grayscale(100%) brightness(100000000%);
}

.tab-connector.selected{
	display: inline;
	display: initial;
}

.desktop-tabs.selected{
	background-color: #003459;
}
.map-container{
	border: 1px solid #003459;
	padding: 16px;
	margin-left: 10px;
	height: 498px; 
	display: flex;
	flex-direction: column;
}

.map-container .title{
	margin-bottom: 16px;
	font-family: 'Open Sans', sans-serif;
	font-size: 14pt;
	font-weight: 700;
	/*display: none;*/
}

.map-container-inner{
	position: relative;
	width: 100%;
	height: 100%;
}

@media (max-width: 960px){
	.map-container{
		margin-left: 0;
		margin-top: 10px;
		margin-bottom: 10px;
	}
}
.left-col{
/*	background-color: green;*/
	float: left;
	width: 60%;
}

.right-col{
	/*background-color: purple;*/
	float: right;
	width: 40%;
}

.desktop-container{
	max-width: 1280px;
	/*background-color: yellow;*/
	margin: auto;
	padding-left: 25px;
	padding-right: 25px;
}

@media (max-width: 960px){
	.left-col{
		float: none;
		width: 100%;
	}

	.right-col{
		float: none;
		width: 100%;
	}
}

.inline-block{
	display: inline-block;
}

.purple {
	background-color: purple;
}

.green {
	background-color: green;
}

.float-right{
	float: right;
}

.float-left{
	float: left;
}

.clear-fix{
	overflow: auto;
}

.position-absolute{
	position: absolute;
}
